import React, { Component } from 'react';
import Article from './article/Article';

class Feed extends Component {
  render() {
    let articles = [];
    for(let article of TESTFEED) {
      articles.push(<Article article={article}></Article>);
    }

    return (
      <div className="Feed">
        {articles}
      </div>
    );
  }
}

